  @import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap');

* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: white;
}

.product_div .container_para{
  justify-content: center;
  text-align: center;
}

.form_input_div{
  /* margin-top: 10px; */
  display: flex;
  flex-wrap: wrap;
  /* text-align: center; */
}

.form_input_para{  
  width: 320px;
  height: 200px;
  margin-bottom: 20px;
  /* background-color: gray; */
  /* margin: 10px; */
}
.product_div p{
  margin: 1px;
  justify-content: center;
  /* text-align: center; */
  display: inline;
  font-weight: 500;
  font-size: 12px;
}
.product_div .form_input_div{
    display: inline;
  font-weight: 500;
  font-size: 12px;
}

.form_input_para img{
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.product_div{
  /* border: 1px solid black; */
  margin: 10px;
  width: 322px;
  height: auto;
  box-shadow: 1px 1px 5px black;
  /* padding: 10px; */
  border-radius: 10px;
  background-color: white;
}
.product_div:hover{
  box-shadow: 10px 10px 10px black;
}
/* .product_div a{  
  display: inline;
} */

@media(max-width:1050px){
  .form_input_para{
    width: 100%;
    /* height: 30%; */
    justify-content: center;
  }
  .product_div{
    margin: 20px;
  width: 90%;
  height: 90%;
  box-shadow: 1px 1px 15px black;
  /* padding: 10px; */
  border-radius: 10px;
  }
  .product_div p{
    margin: 2%;
    display: inline;
    font-weight: auto;
    font-size: 12px;
  }
  .product_div .form_input_div{
    display: inline;
  font-weight: 100;
  font-size: 10px;
  padding: 5px;
}
}
@media(max-width:350px){
  .form_input_para{
    width: 100%;
    justify-content: center;
  }
  .product_div{
    margin: 15px;
  width: 90%;
  height: 90%;
  box-shadow: 1px 1px 15px black;
  /* padding: 10px; */
  border-radius: 10px;
  }
  .product_div p{
    margin: 2%;
    display: inline;
    font-weight: auto;
    font-size: 12px;
  }
  .product_div .form_input_div{
    display: flex;
  font-weight: 100;
  font-size: 10px;
  padding: 5px;
}
}

  